<template>
	<div class="total">
		<div class="content">
			<div class="content-title">修改密码</div>
			<div class="content-mid">
				<div class="item" v-for="item in items">
					<div class="item-left">{{item.title}}</div>
					<v-text-field :label="item.title" outlined dense v-model="item.value" style="margin-left: 5%;"
						color="lightgray" :rules="item.rules" :error-messages="item.errorMessage"></v-text-field>
				</div>
			</div>
			<div class="content-bottom">
				<v-btn color="#009688" @click="summit()">立即提交</v-btn>
			</div>
		</div>
		<!-- 错误提示框 -->
		<v-dialog v-model="isShow" max-width="250">
			<v-card>
				<v-card-title>
					<span class="headline">错误提示</span>
				</v-card-title>
				<v-card-text>{{errMessage}}</v-card-text>
				<v-card-actions>
					<v-spacer></v-spacer>
					<v-btn class="blue darken-1" text @click="isShow = false">确定</v-btn>
				</v-card-actions>
			</v-card>
		</v-dialog>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				account: "",
				passwordCheck: true,
				// 控制错误提示框是否显示
				isShow: false,
				errMessage: "",
				//存放输入框信息以及规则
				items: [{
						title: "原密码",
						value: "",
						rules: [value => !!value || '请输入原密码'],
						errorMessage: "",
					},
					{
						title: "新密码",
						value: "",
						rules: [value => !!value || '请输入新密码'],
						errorMessage: "",
					},
					{
						title: "确认密码",
						value: "",
						rules: [value => !!value || '请输入确认密码'],
						errorMessage: ""
					},
				]
			}
		},
		created() {
			// 页面启动获得账号，if是因为权限问题，如果被篡改路由则跳转404页面
			if (localStorage.getItem("marker") == 2) {
				this.account = this.$route.params.account
			} else {
				this.$router.push({
					name: '404'
				})
			}
		},
		methods: {
			// 检测两次密码是否一样
			check() {
				if (this.items[1].value != this.items[2].value) {
					this.passwordCheck = false
					return false
				} else {
					this.passwordCheck = true
					return true
				}
			},

			//立即提交
			summit() {
				// 调用check方法，将返回值
				let flag = this.check()
				if (flag) {
					// 修改密码接口
					this.$axios({
						method: 'post',
						url: this.$url + 'updatePassword',
						data: {
							"lastPassword": this.items[0].value,
							"account": this.account,
							"newPassword": this.items[2].value
						}
					}).then((res) => {
						if (res.data.code == "FAIL_CODE") {
							this.isShow = true
							this.errMessage = res.data.message
						} else {
							//修改成功后浏览器弹出确认框，点击跳转到登录页面
							window.alert("密码修改成功，点击确定跳转登录页面")
							this.$router.push({
								name: "Login"
							})
						}
					})
				} else {
					// 如果两次密码不一样,就提示确认信息
					this.isShow = true
					this.errMessage = "请确认输入信息是否正确"
				}
			}
		}
	}
</script>

<style scoped>
	* {
		padding: 0;
		margin: 0;
		box-sizing: border-box;
	}

	.total {
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
	}

	.content {
		margin-top: 2%;
		width: 35%;
		height: 65%;
	}

	.content-title {
		width: 100%;
		height: 10%;
		text-align: center;
		font-size: 30px;
	}

	.content-mid {
		margin-left: -8%;
		padding: 20px 0 0 0;
		width: 100%;
		height: 55%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-between;
	}

	.item {
		width: 80%;
		height: 40px;
		display: flex;
		justify-content: space-between;
		font-weight: 500;
	}

	.item-left {
		width: 20%;
		height: 100%;
		line-height: 40px;
		text-align: right;
	}

	.item-right {
		width: 70%;
		height: 100%;
	}

	.item-right-input {
		width: 100%;
		height: 100%;
		border: 1px solid lightgrey;
		outline: none;
		border-radius: 3px;
	}

	.content-bottom {
		margin-top: 7%;
		width: 100%;
		height: 10%;
		display: flex;
		justify-content: space-around;
		align-items: center;
	}
</style>
